"use client";

export default function SportTabs({ active, setActive }) {
  const sports = ["nba", "nfl", "nhl"];

  return (
    <div className="flex gap-2 mb-4">
      {sports.map((s) => (
        <button
          key={s}
          onClick={() => setActive(s)}
          className={`px-4 py-2 rounded-lg font-medium border 
            ${
              active === s
                ? "bg-[#09f755] text-black border-[#09f755]"
                : "bg-[#111113] border-gray-700 text-gray-300"
            }`}
        >
          {s.toUpperCase()}
        </button>
      ))}
    </div>
  );
}
